import { defineComponent, onMounted, reactive } from 'vue';
import headerTitle from './components/headerTitle.vue';
import { useStore } from 'vuex';
import { useRouter, useRoute } from 'vue-router';
export default defineComponent({
  name: 'PerSection',
  components: {
    headerTitle
  },

  setup() {
    const router = useRouter();
    const route = useRoute();
    let menus = reactive({
      cards: [{
        icon: require('@/assets/icons/home/ybm.png'),
        callback: () => {
          console.log('triggle cards1');
        }
      }, {
        icon: require('@/assets/icons/home/czhk.png'),
        callback: () => {
          console.log('triggle cards2');
        }
      }],
      list: [{
        title: '我的订单',
        sub: '查看全部门诊、住院订单',
        icon: require('@/assets/icons/home/dd.png'),
        callback: () => {
          const {
            params: {
              idno,
              name,
              creditUserId
            }
          } = route;
          router.push(`/orders/${creditUserId}/${name}/${idno}`);
        }
      }, {
        title: '亲情付',
        sub: '您可以为家中亲属代付就医费用',
        icon: require('@/assets/icons/home/qq.png'),
        callback: () => {
          const {
            params: {
              idno,
              name,
              creditUserId
            }
          } = route;
          router.push(`/family/${creditUserId}/${name}/${idno}`);
        }
      }, {
        title: '还款记录',
        sub: '您可以随时查询历史还款记录',
        icon: require('@/assets/icons/home/hk.png'),
        callback: () => {
          console.log('还款记录 3');
        }
      }]
    });
    const store = useStore();
    let user = reactive({
      name: '初始化用户',
      outCurrentCreditLimit: '0.00',
      hosCurrentCreditLimit: '0.00'
    });
    onMounted(() => {
      const {
        userInfo
      } = store.state.interModule;
      Object.keys(user).forEach(key => {
        user[key] = userInfo[key] || user[key];
      });
    });
    return {
      menus,
      user
    };
  }

});